.container {
	display: flex;
	width: 100%;
	height: 100vh;
	flex-direction: column;
}

.tabMenuBox {
	display: flex;
	width: 100%;
	height: 100px;
	border-bottom: 1px black solid;
	justify-content: center;
}

.logoBox {
	display: flex;
	flex: 1;
	height: 100%;
	align-items: center;
	font-size: 30px;
}

.tabMenuInnerBox {
	display: flex;
	max-width: 900px;
	width: 100%;
	height: 100%;
	align-items: center;
}

.contentsArea {
	display: flex;
	flex: 1;
	max-width: 900px;
	width: 100%;
	/* height: 100%; */
	margin: 0px auto;

}

.routerBox {
	display: flex;
	max-width: 700px;
	width: 100%;
	align-items: center;
	flex-direction: column;
}


.categoryArea {
	display: flex;
	flex-direction: column;
	width: 200px;
	height: 100%;
	padding-top: 50px;
}

.categoryItem {
	display: inline-flex;
	width: auto;
	box-sizing: border-box;
	padding: 10px;
	background-color: blue;
	border-radius: 30px;
	margin-top: 10px;
}


.routerItem {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100px;
	border: 1px black solid;
	width: 100%;
	flex-direction: column;
}